@import "../../../varibles";

.wrapper {
	position: relative;
	height: calc(100vh - 78px);
	display: flex;

	.main {
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		position: relative;

		&::before {
			content: "";
			background-image: linear-gradient(to right, #0f1011, transparent);
			width: 150%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
		}
	}

	.info {
		position: relative;
		z-index: 1;
		width: 50%;
		padding: 4rem 0 4rem 1rem;

		.additional {
			display: flex;
			align-items: center;
			justify-content: space-between;
			color: $gray;
			margin: 1rem 0;

			> span {
				margin-right: 5rem;
				font-size: 18px;
			}
		}

		.description {
			font-size: 20px;
			color: $gray;
			margin-bottom: 3rem;
		}
	}
}
